import { styled, alpha } from '@mui/material/styles';
import {
  Button,
  IconButton,
  ListItemIcon,
  ListItemText,
  Menu,
  MenuItem,
  Tooltip,
} from '@mui/material';
import React, { useContext, useState } from 'react';
import { AppContext } from 'MainApp';

const StyledMenu = styled(props => (
  <Menu
    elevation={0}
    anchorOrigin={{
      vertical: 'bottom',
      horizontal: 'right',
    }}
    transformOrigin={{
      vertical: 'top',
      horizontal: 'right',
    }}
    {...props}
  />
))(({ theme }) => ({
  '& .MuiPaper-root': {
    borderRadius: 6,
    marginTop: theme.spacing(1),
    minWidth: 180,
    color: theme.palette.mode === 'light' ? 'rgb(55, 65, 81)' : theme.palette.grey[300],
    boxShadow:
      'rgb(255, 255, 255) 0px 0px 0px 0px, rgba(0, 0, 0, 0.05) 0px 0px 0px 1px, rgba(0, 0, 0, 0.1) 0px 10px 15px -3px, rgba(0, 0, 0, 0.05) 0px 4px 6px -2px',
    '& .MuiMenu-list': {
      padding: '4px 0',
    },
    '& .MuiMenuItem-root': {
      '& .MuiSvgIcon-root': {
        fontSize: 18,
        color: theme.palette.text.secondary,
        marginRight: theme.spacing(1.5),
      },
      '&:active': {
        backgroundColor: alpha(theme.palette.primary.main, theme.palette.action.selectedOpacity),
      },
    },
  },
}));

export const ConfMenu = ({ icon, startIcon, title, action, from, item }) => {
  const {
    env: { environnements },
  } = useContext(AppContext);

  const [anchorEl, setAnchorEl] = useState(null);
  const open = Boolean(anchorEl);
  const handleClick = event => {
    if (environnements.length > 1) setAnchorEl(event.currentTarget);
    else action(environnements[0].conf);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
    <>
      {!startIcon && icon && (
        <Tooltip title={title}>
          <IconButton
            onClick={handleClick}
            id="button-title"
            aria-controls={open ? 'demo-customized-menu' : undefined}
            aria-haspopup="true"
            aria-expanded={open ? 'true' : undefined}
          >
            {icon}
          </IconButton>
        </Tooltip>
      )}
      {!icon && !item && (
        <Button
          onClick={handleClick}
          startIcon={startIcon}
          disableElevation
          variant="contained"
          aria-controls={open ? 'demo-customized-menu' : undefined}
          aria-haspopup="true"
          aria-expanded={open ? 'true' : undefined}
        >
          {title}
        </Button>
      )}
      {!icon && item && (
        <MenuItem onClick={handleClick}>
          {startIcon && <ListItemIcon>{startIcon}</ListItemIcon>}
          <ListItemText>{title}</ListItemText>
        </MenuItem>
      )}
      <StyledMenu
        id="button-title"
        MenuListProps={{
          'aria-labelledby': 'button-title',
        }}
        anchorEl={anchorEl}
        open={open}
        onClose={handleClose}
      >
        {environnements.map(({ name, conf }) => (
          <MenuItem
            key={name}
            onClick={() => {
              handleClose();
              action(conf);
            }}
            disableRipple
          >
            {`${from ? 'À partir de' : 'Dans'} l'environnement "${name}"`}
          </MenuItem>
        ))}
      </StyledMenu>
    </>
  );
};
